<template>
  <div class="item">
    <div class="item-content">
      <router-link
        v-if="item.author.loginname"
        :to="{ name: 'user', params: { loginname: item.author.loginname } }"
      >
        <img :src="item.author.avatar_url" alt="" />
      </router-link>
      <div class="cell" v-if="item.visit_count">
        <span class="reply">{{ item.reply_count }}</span
        >/
        <span class="reads">{{ item.visit_count }}</span>
      </div>
      <span class="top active" v-if="item.top">置顶</span>
      <span class="top active" v-else-if="item.good">精华</span>
      <span class="top" v-else-if="$route.path === '/all'">{{
        item.tab | transformTab
      }}</span>

      <router-link
        class="title"
        :to="{
          name: 'details',
          params: {
            id: item.id,
          },
        }"
        >{{ item.title }}</router-link
      >
      <span class="time">{{ item.last_reply_at | moment("from", "now") }}</span>
    </div>
  </div>
</template>

<script>
import moment from "moment";
moment.locale("zh-cn");
export default {
  props: ["item"],
};
</script>

<style lang="less">
.item .item-content {
  background-color: #fff;
  border-bottom: 1px solid #f0f0f0;
  padding: 8px;
  display: flex;
  align-items: center;
  img {
    width: 30px;
    height: 30px;
    border-radius: 5px;
    margin-right: 8px;
    flex-shrink: 0;
  }
  .cell {
    width: 85px;
    .reply {
      color: purple;
      flex-shrink: 0;
    }
    .reads {
      color: #ccc;
      font-size: 12px;
    }
  }
  .top {
    width: 34px;
    line-height: 18px;
    background-color: #e8f3ff;
    color: #1d7bf6;
    font-size: 12px;
    border-radius: 5px;
    text-align: center;
    margin-right: 8px;
    flex-shrink: 0;
  }
  .title {
    flex-grow: 1;
    display: block;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    color: #000;
  }
  .active {
    background-color: #1e80ff;
    color: #fff;
  }
  .time {
    color: #ccc;
    width: 70px;
    font-size: 12px;
    flex-shrink: 0;
  }
}
</style>
